import {Meteor} from 'meteor/meteor';

export default {
    Query: {
        campaign(root, args, context) {
            const userID = context.userId || "zfbP3aTqiP8yAg8NQ";
            console.log("CAMPAIGN",args);
            const campaign = Campaigns.findOne({_id: args._id});
            if (userID === campaign.owner) {
                console.log("IsOwner");
            }
            return campaign;

        },
        campaignsOwn(root, args, context){
            const userID = context.userId || "zfbP3aTqiP8yAg8NQ";

            let query = {
                owner: userID
            };

            return Campaigns.find(query).fetch();
        }
    },
    Campaign:{
        owner: (account) => {
            let user = Meteor.users.findOne({_id: account.owner}, {fields: {services: 0}});
            return user
        },
        manager: (account) => {
            let user = Meteor.users.findOne({_id: account.manager}, {fields: {services: 0}});
            return user
        },
        shares: (account) => {
            let users = Meteor.users.find({_id: {$in: account.shares} }, {fields: {services: 0}}).fetch();
            return users
        },
    },
    CampaignInformation:{
        brands: (account) => {
            if(!account.brands || account.brands.length===0){
                return [];
            }
            let result = SocialAccounts.find({
                _id: {$in: account.brands}
            }, {fields: {connection: 0}}).fetch();
            return result
        }
    }
};